<template>
  <div>
    <h1>test_composition</h1>
    <div>{{ counter }}</div>
    <div>{{ text }}</div>
    <div v-for="data in arr" :key="data.key">
      {{ data.key }} : {{ data.value }}
    </div>
    <button @click="onClick()">click</button>
  </div>
</template>

<script>
// 접속: http://localhost:8080/test_composition
// 설명페이지: https://v3.ko.vuejs.org/api/composition-api.html#%E1%84%85%E1%85%A1%E1%84%8B%E1%85%B5%E1%84%91%E1%85%B3%E1%84%89%E1%85%A1%E1%84%8B%E1%85%B5%E1%84%8F%E1%85%B3%E1%86%AF-%E1%84%92%E1%85%AE%E1%86%A8-lifecycle-hooks
import { ref, onUnmounted } from "vue";

export default {
  setup() {
    let counter = ref(0);
    let text = ref("text");
    let arr = ref([
      { key: "k", value: "1" },
      { key: "kk", value: "2" },
      { key: "kkk", value: "3" },
    ]);

    function onClick() {
      console.log("onClick", counter);
      counter.value++;
      text.value += counter.value;
      arr.value.pop();
    }

    onUnmounted(() => {
      console.log("unmounted!");
    });

    return {
      counter,
      text,
      arr,
      onClick,
    };
  },
};
</script>
